import * as EmojisURL from "../content/emojiURL";
import * as Learning from "../content/learning";
import * as Dom from "../content/dom";

const initialState = {
  items: [EmojisURL, Learning, Dom]
};

export default (state = initialState, { type, payload }) => {
  switch (type) {
    default: {
      return state;
    }
  }
};



