export const image =
  "https://images.pexels.com/photos/159278/launch-pad-music-live-band-stage-159278.jpeg?w=1260&h=750&auto=compress&cs=tinysrgb";

export const title = "Flippour";

export const content = `
  A simple puzzle-esque mobile game for Android and iOS
  
  The game is very simple. The user is presented with a color and has to tap all squaresof that color before the time runs out. Each square correctly tapped is +1 while a squareincorrectly tapped is -10. If the score reaches below zero the game is ended. If the timer runs out the game is ended. Each ten levels the totaly level time it dropped by one seconds,i.e. levels 1 - 10 the user has ten seconds to complete the level, while levels 11 - 20 the userhas nine seconds to complete the level.
  
  This project implements React Native's Animated API along with the option "useNativeDriver" for usinganimation on the native level to ensure the user does not experience any jank on the main UI thread alongwith the JS thread. High performance was a big piece of this project and it's working beautifully.
  
  Originially this project was mean to try out Facebook and Expo's "create-react-native-app" platform, buthas since been abonded in favor of Facebook's original react-native CLI.
  
  You can find Play Store and App Store links <a href="https://mini-eggs.github.io/Flippour/">here</a>.
  
  This project has been open sourced. You can find the code <a href="https://github.com/mini-eggs/Flippour">here</a>.
`.trim();



