import React from "react";

export default componentPromise =>
  class extends React.Component {
    constructor(props) {
      super(props);
      this.state = { component: null };
      componentPromise().then(component => this.handleLoad(component));
    }

    handleLoad(component) {
      const NextComponent = component.default;
      this.setState(() => ({ component: <NextComponent {...this.props} /> }));
    }

    render() {
      return this.state.component;
    }
  };



