import React from "react";
import { withEmit } from "react-emit";
import { connect } from "react-redux";
import SingleLink from "./link";

const styles = {
  header: {
    padding: "50px 0"
  },
  title: {
    padding: "0 0 50px"
  },
  linkContainer: {
    textAlign: "right"
  },
  link: {
    padding: "10px 15px",
    marginLeft: "15px",
    borderRadius: "5px",
    color: "black"
  }
};

const linkAttributes = {
  className: "touchable",
  style: styles.link
};

const component = ({ links, emit }) =>
  <header style={styles.header}>
    <div style={styles.title}>
      <h2>Evan Jones,</h2>
      <h3>web/app developer</h3>
    </div>
    <div style={styles.linkContainer}>
      <img
        alt="navigation-icon"
        className="menu"
        src={require("../images/ic_menu_black_24px.svg")}
        onClick={() => emit("navigation:toggle")}
      />
      {/* <div className="menu-container" onClick={() => emit("navigation:toggle")}>
        <div className="menu" />
      </div> */}
      {links.map(SingleLink(linkAttributes))}
    </div>
  </header>;

const mapState = ({ Links }) => ({ links: Links.header });

export default connect(mapState)(withEmit(component));



