import * as Flippour from "../content/flippour";
import * as Triangly from "../content/triangly";
import * as SsrCra from "../content/ssrcra";

const initialState = {
  items: [Flippour, Triangly, SsrCra]
};

export default (state = initialState, { type, payload }) => {
  switch (type) {
    default: {
      return state;
    }
  }
};



