import React from "react";
import { connect } from "react-redux";
import { withEmit } from "react-emit";
import SingleLink from "./link";

const styles = {
  linksContainer: {
    justifyContent: "center",
    display: "flex",
    flex: 1,
    flexDirection: "column"
  },
  link: {
    display: "block",
    lineHeight: "50px",
    margin: "0 auto",
    color: "black",
    cursor: "pointer",
    width: "100%",
    minWidth: "200px",
    padding: "0 15px"
  }
};

const linkAttributes = {
  className: "hover",
  style: styles.link
};

const toggle = ({ active }) => ({ active: !active });

class Navigation extends React.Component {
  constructor(props) {
    super(props);
    this.state = { active: false, hiding: false };
  }

  componentDidMount() {
    this.props.on("navigation:toggle", () => this.setState(toggle));
  }

  hide() {
    if (this.state.hiding) return;
    this.setState(() => ({ hiding: true }));
    setTimeout(() => {
      this.setState(() => ({ hiding: false, active: false }));
    }, 400);
  }

  render() {
    if (!this.state.active) return null;

    return (
      <nav
        className={this.state.hiding ? "hide" : null}
        onClick={() => this.hide()}
      >
        <div style={styles.linksContainer}>
          {this.props.links.map(SingleLink(linkAttributes))}
        </div>
      </nav>
    );
  }
}

const mapState = ({ Links }) => ({ links: Links.header });

export default connect(mapState)(withEmit(withEmit(Navigation)));



