import React from "react";
import { Link } from "react-router-dom";

export default linkAttributes => ({ title, href, to }, index) =>
  href
    ? <a key={index} href={href} children={title} {...linkAttributes} />
    : <Link key={index} to={to} children={title} {...linkAttributes} />;



