import React from "react";
import { Route, Switch } from "react-router";
import { HashRouter } from "react-router-dom";
import { createStore, combineReducers } from "redux";
import { Provider } from "react-redux";
import { EmitProvider } from "react-emit";

import Links from "./reducers/links";
import Pages from "./reducers/pages";
import Posts from "./reducers/posts";
import Projects from "./reducers/projects";

import WithPromise from "./components/withPromise";
import Entry from "./scenes/entry";
import Header from "./components/header";
import Navigation from "./components/navigation";
const ProjectsLazy = WithPromise(() => import("./scenes/projects"));
const PostsLazy = WithPromise(() => import("./scenes/posts"));
const ContentLazy = WithPromise(() => import("./scenes/content"));
const reducers = combineReducers({ Links, Pages, Posts, Projects });

export default (
  <Provider store={createStore(reducers)}>
    <EmitProvider>
      <HashRouter>
        <div>
          <div className="container">
            <Header />
            <Navigation />
            <Switch>
              <Route exact path="/" component={Entry} />
              <Route path="/:type/:id" component={ContentLazy} />
              <Route path="/projects" component={ProjectsLazy} />
              <Route path="/posts" component={PostsLazy} />
            </Switch>
          </div>
          <footer />
        </div>
      </HashRouter>
    </EmitProvider>
  </Provider>
);



