export const image =
  "https://images.pexels.com/photos/532571/pexels-photo-532571.jpeg?w=1260&h=750&auto=compress&cs=tinysrgb";

export const title = "Learning in 2016 and Onward";

export const content = `
    I had some minor Twitter drama recently (12/31/2016) about Javascript tooling. Here's the original tweet's text:
    
    'Have we all just over complicated the web? We don't need webpck, sass, iconfonts and all these things just to put a form on a screen.'
    
    I replied with:
    
    'You've been on /r/javascript too much my friend. ;-)'
    
    and was shortly blocked.
    
    Although, he had a very good point for some forms of web development (i.e. static sites, Wordpress, etc.), this isn't really applicable to a lot of what I've been doing in 2015 and onward. Tools such as Webpack, Gulp, Rollup, Sass, and various CLIs come in handy, a lot, but not for creating websites. 
    
    Tools such as these become necessary with complex web applications (Google Docs, Netflix, the application my dentist uses to keep track of patient recrods, etc.). I would never think to add React and Webpack to some of our closer-to-static sites we maintain at my current employer.
    
    Even if you're not currently making web applications, but instead primiraly web sites: learn webpack, learn gulp, sass, react-scripts CLI, learn. (!) You might not need the information, but if you want to progress your career then learn. 
    
    The type of attitude seen in the tweet above is the same attitude my peers while a sophmore in high school geometry had when saying:
    
    'Are we ever going to use this in real life?'
    
    Maybe! And if you do you'll be glad you already have the knowledge under your belt.
    
    Love learning in 2017.
`.trim();



