export const image =
  "https://images.pexels.com/photos/327070/pexels-photo-327070.jpeg?w=1260&h=750&auto=compress&cs=tinysrgb";

export const title = "Emojis and the URL";

export const content = `
    NOTE: I've since updated this website and no longer support emoji in the URL.
    
    Emojis can be in the URL!!! Well, kind of.         
    
    Emojis can be in path names but not domain names, at least not of major domain names (.com, .org, .co.uk). The only domain with full browser support is .ws. Check out 💩.ws for en example of how this behave/looks in browser.
    
    If you do get a chic domain name with an emoji in it: how do you navigate to the URL while on the computer? Another gotcha.
    
    The only way I'll be experimenting with Emojis in the domain name is if I have another domain that redirects to the Emoji domain--annoying.
    
    But hey, look at the URl bar now. ☺️
`.trim();



