import React from "react";
import FadeIn from "background-image-fade-in";

export default class extends React.Component {
  componentDidMount() {
    FadeIn(".background-image", 250);
  }

  render() {
    const { title, content, image } = this.props;
    const attr = {};
    attr["data-src"] = image;

    return (
      <div>
        <h1>
          {title}
        </h1>
        <section>
          <div className="background-image" {...attr} />
          <article>
            <div dangerouslySetInnerHTML={{ __html: content }} />
          </article>
        </section>
      </div>
    );
  }
}



